.category-list{
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    padding: 10px 0 10px 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    font-size: 20px;
}

.category-list-items{
    padding-left: 15px;
    margin: 0 6px 6px 0 ;
    cursor: pointer;
    
}

.selected{
    color: lightgreen;
    text-decoration: underline;
    text-underline-offset: 10px;
}
